import React, { useEffect, useState } from "react";
import { axiosGet } from "../../services/axiosService";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "../../redux/User";
import environment from "../../constants/Environment";
import useGetAllWorkSpaces from "@/hooks/useGetWorkSpaces";
import useGetRecentBots from "@/hooks/useGetRecentBots";
import { useQueryClient } from "@tanstack/react-query";

interface PopUp {
  isOpen: boolean;
  onClose?: () => void;
  receivedIndex: any;
  removeClassFromBody: () => void;
}

const WorkspaceMbDropdown = ({
  isOpen,
  onClose,
  receivedIndex,
  removeClassFromBody,
}: PopUp) => {
  const dispatch = useDispatch();
  const queryClient = useQueryClient();
  // const [bots, setBots] = useState<any>([]);
  let bots: any[] = [];
  // const [workspace, setWorkspace] = useState<any>([]);
  // let workspace: any = [];
  const [reload, setReload] = useState<boolean>(false);
  const allWorkSpaces = useGetAllWorkSpaces();
  const recentBots = useGetRecentBots();

  if(recentBots.isSuccess){
    let botData = recentBots.data;
      if (environment().isAI) {
        botData = botData?.filter((bot) => bot.botType === "ai");
      } else if (window.location.hostname.split(".")[1] === "engage") {
      } else {
        botData = botData?.filter((bot) => bot.botType === "web" || bot.botType === "campaign");
      }
    bots = botData;
  }

  // if(allWorkSpaces.isSuccess){
  //   workspace = allWorkSpaces.data;
  // }

  // useEffect(() => {
  //   axiosGet(`/users/allRecentBot`)
  //     .then((response) => {
  //       let botData = response.data.data;
  //       if (environment().isAI) {
  //         botData = botData?.filter((bot) => bot.botType === "ai");
  //       } else if (window.location.hostname.split(".")[1] === "engage") {
  //       } else {
  //         botData = botData?.filter((bot) => bot.botType === "web");
  //       }
  //       setBots(botData);
  //     })
  //     .catch((err) => console.log(err));
  
  //   axiosGet("/workspace/getAllWorkspaces")
  //     .then((response) => setWorkspace(response.data.data))
  //     .catch((err) => console.log(err));
  // }, [reload]);

  const addBotToWorkspace = (value, value1) => {
    dispatch(userSlice.actions.setReload(true));
    axiosGet(
      `/workspace/addBotToWorkspace?workspaceId=${value1._id}&botId=${value._id}`
    )
      .then((response) => {
        queryClient.invalidateQueries({ queryKey: ["bots"] });
        queryClient.invalidateQueries({ queryKey: ["workSpaces"] });
        setReload(!reload);
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className={isOpen ? "popup-visible" : "popup-hidden"}>
      <div className="mb-overlay"></div>
      <div className="mb-dropdown-container">
        <div className="mb-dropdown-heading">
          <p>Workflow name</p>
          <svg
            className="close-mbWorkspace"
            onClick={() => {
              if (onClose) onClose(), removeClassFromBody();
            }}
            xmlns="http://www.w3.org/2000/svg"
            width="16"
            height="16"
            viewBox="0 0 16 16"
            fill="none"
          >
            <g clipPath="url(#clip0_6088_722385)">
              <path
                d="M9.46584 8.01167L15.6959 1.78141C16.1014 1.37615 16.1014 0.720912 15.6959 0.315659C15.2907 -0.0895946 14.6354 -0.0895946 14.2302 0.315659L7.9999 6.54592L1.76982 0.315659C1.36438 -0.0895946 0.709336 -0.0895946 0.304082 0.315659C-0.101361 0.720912 -0.101361 1.37615 0.304082 1.78141L6.53416 8.01167L0.304082 14.2419C-0.101361 14.6472 -0.101361 15.3024 0.304082 15.7077C0.506045 15.9098 0.771594 16.0114 1.03696 16.0114C1.30232 16.0114 1.56768 15.9098 1.76982 15.7077L7.9999 9.47741L14.2302 15.7077C14.4323 15.9098 14.6977 16.0114 14.963 16.0114C15.2284 16.0114 15.4938 15.9098 15.6959 15.7077C16.1014 15.3024 16.1014 14.6472 15.6959 14.2419L9.46584 8.01167Z"
                fill="#6D6D6D"
              />
            </g>
            <defs>
              <clipPath id="clip0_6088_722385">
                <rect width="16" height="16" fill="white" />
              </clipPath>
            </defs>
          </svg>
        </div>
        {bots.map((value, index) =>
          receivedIndex == index ? (
            <ul className="mb-dropdown-list">
              {allWorkSpaces.data?.map(
                (value1, index1) =>
                  value1.status === "active" && (
                    <li
                      key={index1}
                      onClick={() => {
                        if (onClose) onClose();
                        removeClassFromBody();
                        addBotToWorkspace(value, value1);
                      }}
                    >
                      {value1.workspaceName.slice(0, 1) +
                        value1.workspaceName.slice(1)}
                    </li>
                  )
              )}
            </ul>
          ) : null
        )}
      </div>
    </div>
  );
};

export default WorkspaceMbDropdown;
