import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "@/redux/User";
import { useNavigate } from "react-router-dom";
import { axiosPost } from "@/services/axiosService";
import WrkflowAnimate from "@/assets/WrkflowAnimate";
import { WebBot, Campaign } from "@/assets/WorkFlowIcons";
import environment from "@/constants/Environment";
import * as GtmService from "@/services/gtmService";
import { State } from "@/redux/Types";
import { useQueryClient } from "@tanstack/react-query";
import "@/pages/authentication/authentication.css";
import CampaignGif from "@/assets/CreateBotSvgs/CampaignGif";
import WebBotGif from "@/assets/CreateBotSvgs/WebBotGif";

const environmentData = environment();

interface Props {
  onClose: () => void;
}

export default function CreateBot({ onClose }: Props) {
  const router = useNavigate();
  const dispatch = useDispatch();
  const queryClient = useQueryClient();
  const userEmail = useSelector((state: State) => state.user.userEmail);
  const compDetails = useSelector((state: State) => state.user.companyDetails);
  const [botName, setBotName] = useState("");
  const [botNameValid, setBotNameValid] = useState(true);
  const [botType, setBotType] = useState<BotType["id"]>(
    environmentData.isAI ? "ai" : "web"
  );
  const [loader, setLoading] = useState(false);
  const [hoveredBot, setHoveredBot] = useState("");
  const [campaignState, setCampaignState] = useState(true);
  const [webState, setWebState] = useState(true);
  useEffect(() => {
    if (campaignState) {
      setTimeout(() => {
        setCampaignState(false);
      }, 5000);
    }
  }, [campaignState]);

  useEffect(() => {
    if (webState) {
      setTimeout(() => {
        setWebState(false);
      }, 5000);
    }
  }, [webState]);

  async function handleBotCreate() {
    if (loader) return;
    const name = botName.trim();
    if (name.length < 3 || name.length > 30) {
      setBotNameValid(false);
      return false;
    }

    try {
      setLoading(true);
      const response = await axiosPost("/users/createBot", {
        projectName: name,
        botType,
      });

      queryClient.invalidateQueries({ queryKey: ["bots"] });
      queryClient.invalidateQueries({ queryKey: ["workSpaces"] });

      const { projectName, botCustomId, url } = response.data.data;
      await axiosPost("/audience/lists", {
        name: projectName,
        list_id: botCustomId,
        type: "bot",
      });

      GtmService.pushToDataLayer({
        event: "chatbot_created",
        user_email: userEmail ?? "",
        user_id: compDetails.userId ?? "",
        company_id: compDetails._id ?? "",
        company_name: compDetails.compName ?? "",
        bot_id: botCustomId ?? "",
        bot_name: projectName ?? "",
        bot_type: "bot",
      });

      onClose();
      router(`/builder/${url}`);
    } catch (error: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            error.response?.data.message ||
            error.message ||
            "Something went wrong",
        })
      );
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    const name = botName.trim();
    if (!botNameValid) {
      if (name.length < 3 || name.length > 30) {
        setBotNameValid(false);
      } else {
        setBotNameValid(true);
      }
    }
  }, [botName]);

  const reloadSvg = (event) => {
    if (hoveredBot === "campaign") {
      if (!campaignState) {
        setCampaignState(true);
        const item = event.currentTarget;
        const parent = item.parentNode;
        const next = item.nextSibling;
        parent?.removeChild(item);
        parent?.insertBefore(item, next);
      }
    }
    if (hoveredBot === "web") {
      if (!webState) {
        setWebState(true);
        const item = event.currentTarget;
        const parent = item.parentNode;
        const next = item.nextSibling;
        parent?.removeChild(item);
        parent?.insertBefore(item, next);
      }
    }
  };


  return (
    <div className="createbot-modal modal-scroll" onClick={onClose}>
      <div
        className="createbot-container modal-responsive create-bot-landscape"
        onClick={(event) => event.stopPropagation()}
      >
        <div
          className={`add-wrk-container ${
            environment().isAI ? "" : "add-workflow-wrapper"
          }`}
        >
          {environment().isAI ? (
            <div
              className={`wrkspace-img-cont-dashboard flex-cnt ${
                environment().isAI && "bg-white"
              }`}
            >
              <img
                className="new-bot"
                src="https://dlvkyia8i4zmz.cloudfront.net/y05NnVQRRmVntPnVZdMk_new_ai_bot.svg"
                alt="new bot or workflow"
              />
            </div>
          ) : (
            ""
          )}
          <div
            className="workspace-dt ct-wrkspce"
            style={environment().isAI ? { backgroundColor: "white" } : {}}
          >
            <h1>Create New {environmentData.isAI ? "AI Bot" : "Workflow"}</h1>
          </div>
          <div className="createbot-button no-mb">
            <div
              className={`padding-in-modal  ${
                !environment().isAI ? " pd-30" : ""
              }`}
            >
              <div className="padding-modal-box ct-label">
                <label htmlFor="">
                  {environmentData.isAI ? "AI Bot Name" : "Enter Workflow Name"}
                </label>
                <input
                  type="text"
                  className="create-bot-input"
                  placeholder={
                    environmentData.isAI
                      ? "Enter AI Bot Name..."
                      : "Enter Workflow Name"
                  }
                  value={botName}
                  onChange={(event) => setBotName(event.target.value)}
                  onKeyDown={(event) =>
                    event.key === "Enter" && handleBotCreate()
                  }
                />
                {!botNameValid && botName.length > 0 && (
                  <p className="alert-message">
                    {`${
                      environmentData.isAI ? "AI Bot" : "Workflow"
                    } Name should be ${
                      botName.length < 3 ? `minimum of 3` : `maximum of 30`
                    } characters`}
                  </p>
                )}
              </div>
              {!environmentData.isAI && (
                <>
                  <hr className="mt-20" />
                  <div className="select-type">
                    <label htmlFor="">Select Workflow type</label>
                    <div className="workflow-type fx">
                      {BOT_TYPE.map((type) => (
                        <div
                          key={type.id}
                          className="workflows fx fx-d-col al-cnt p-relative cp"
                          onClick={() => setBotType(type.id)}
                          style={{
                            background: type.bg,
                            border: type.border,
                          }}
                        >
                          <div
                            className={`workflow-box workflow-type-box flex-cnt fx-d-col`} onMouseEnter={(e) => { reloadSvg(e); setHoveredBot(type.id)}}
                          >
                            <div className="wrkflw-head fx jstfy-sp-btw w-100 mb-15">
                              <div className="wrklw-head-left fx gap-10 al-cnt">
                                {type.icon}
                                <h4 className={`workflow-type-heading`}>
                                  {type.name}
                                  {botType === type.id ? (
                                    <RadioIconActive />
                                  ) : (
                                    <RadioIcon />
                                  )}
                                </h4>
                              </div>
                            </div>
                            {type.text && <p className="no-m">{type.text}</p>}
                            {type.imgUrl && (
                              // <object
                              //   type="image/svg+xml"
                              //   data={type.imgUrl}
                              //   
                              // >
                              //   <img src={type.imgUrl} alt="no_bots" />
                              // </object>
                              <div className={`bot_type_svgs mt-30 ${botType === type.id ? "float-img" : ""}`} >
                                {type.imgUrl}
                              </div>
                            )}
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </>
              )}
            </div>
          </div>
          <div
            className={`create-bot-footer flex-cnt  ${
              !environment().isAI ? " no-m" : ""
            }`}
          >
            <button
              type="submit"
              className={
                !botName?.length || !botNameValid ? "valid-workspace" : ""
              }
              disabled={!botName?.length || !botNameValid}
              onClick={handleBotCreate}
            >
              {loader
                ? "Creating..."
                : environmentData.isAI
                ? "Create AI Bot"
                : "Create Workflow"}
            </button>
          </div>
        </div>
        <div
          className={`close new-modal-close flex-cnt ${
            !environment().isAI ? "og-close" : ""
          }`}
          onClick={onClose}
        >
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="#6D6D6D"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
              fill="#6D6D6D"
            />
          </svg>
        </div>
      </div>
    </div>
  );
}

interface BotType {
  id: "web" | "campaign" | "ai";
  name: string;
  icon: JSX.Element;
  bg: string;
  disable?: boolean;
  text?: string;
  imgUrl?: JSX.Element;
  border?: string;
}

