const RadioIcon = () => (
  <svg
    width={18}
    height={18}
    viewBox="0 0 18 18"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <circle cx={9} cy={9} r={8} fill="white" />
    <circle cx={9} cy={9} r={8.5} stroke="black" strokeOpacity={0.2} />
  </svg>
);

const RadioIconActive = () => (
  <svg
    width={19}
    height={18}
    viewBox="0 0 19 18"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <circle cx={9.5} cy={9} r={8.5} fill="white" stroke="#167EFB" />
    <circle cx={9.5} cy={9} r={4.5} fill="#167EFB" stroke="#167EFB" />
  </svg>
);

const BOT_TYPE: Array<BotType> = [
  {
    id: "campaign",
    name: "Campaigns",
    icon: <CampaignIconSvg />,
    bg: "linear-gradient(170deg, rgba(255, 255, 255, 0.10) -11.1%, rgba(239, 171, 16, 0.40) 97.23%)",
    text: "Create your online campaign, tailored to your goals – flexibility meets success.",
    imgUrl: <CampaignGif/>,
    border: "1px solid rgba(239, 171, 16, 0.30)",
  },
  {
    id: "web",
    name: "Web bot",
    icon: <WebBotIconSvg />,
    bg: "linear-gradient(170deg, rgba(255, 255, 255, 0.10) -11.1%, rgba(62, 155, 221, 0.40) 97.23%)",
    disable: environment().isAI ? true : false,
    text: "Create and customize your web bot, your way – simplify and personalise.",
    imgUrl: <WebBotGif/>,
    border: "1px solid #3E9BDD",
  },
];
