import { axiosGet } from "@/services/axiosService";
import { useQuery } from "@tanstack/react-query";

export default function useGetAllWorkSpaces(type: string = "updated") {
  return useQuery({
    queryKey: ["workSpaces", "getAllWorkSpaces", { type }],

    queryFn: async () => {
      const response = await axiosGet(`/workspace/getAllWorkspaces?type=${type}`);
      return response.data.data;
    },
    staleTime: 1000,
  });
}