import React, { Fragment } from "react";
import { useNavigate } from "react-router-dom";
function CouponcodeModal() {
  const route = useNavigate();
  return (
    <Fragment>
      <div className="createbot-modal">
        <div className="craetebot-container">
          <div className="success-cont">
                <div className="succ-header">
                <img src="https://res.cloudinary.com/dnag1wvx8/image/upload/v1663135241/success_icon_group_1_acgo4u.svg" alt="" />
                </div>
                <div className="succ-footer">
                <h1>Coupon successfully applied</h1>
                <span className="no-mb">Congratulations! You've unlocked the plus plan for lifetime.</span>
            
                </div>
                <div className="cross-icon" onClick={() => route('/dashboard')}>
                <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z" fill="#6D6D6D"/>
                </svg>

                </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
}
export default CouponcodeModal;
