import { useNavigate } from "react-router-dom";
import environment from "@/constants/Environment";
import { userSlice } from "@/redux/User";
import { useDispatch, useSelector } from "react-redux";
import { State } from "@/redux/Types";
import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import useFetchAgentChat from "@/pages/agent/hooks/useFetchAgentChat";
import useDeleteAgentChat from "@/pages/agent/hooks/useDeleteAgentChat";

const NewBanner = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const plan: any = useSelector((state: State) => state.user.plan);
  const [lock, setLock] = useState<boolean>(false);
  const [unfinishedChat, setUnfinishedChat] = useState(false);
  const { data: agentChat, fetchStatus: agentFetchStatus } =
    useFetchAgentChat("cynthia");
  const { mutateAsync: deleteChat, status: deleteChatStatus } =
    useDeleteAgentChat("cynthia");
  const [agentChatId, setAgentChatId] = useState<string>("");

  useEffect(() => {
    if (agentChat !== undefined && agentFetchStatus !== "fetching") {
      setUnfinishedChat(
        Boolean(
          agentChat?.chatData?.messages?.find(
            (message) => message.role != "assistant",
          ),
        ),
      );
      setAgentChatId(agentChat?._id);
    }
  }, [agentFetchStatus]);

  useEffect(() => {
    if (plan) {
      if (
        (environment()?.isAI &&
          plan?.ai_currentLimits?.bots <= plan?.ai_currentUsage?.bots) ||
        (!environment()?.isAI &&
          plan?.currentLimits?.bots <= plan?.current_usage?.bots)
      )
        setLock(true);
      else setLock(false);
    }
  }, [plan]);

  return (
    <div className="new_banner_container mb-25">
      <div className="nw_banner_left">
        <div className="fx al-cnt">
          <span className="beta_cbb">Beta</span>
          <div
            // className="try-now-lock"
            className={`try-now-lock ${lock ? "" : ""}`}
          >
            {lock && <LockIcon />}
            <div className="tooltip-trynow fx fx-d-col gap-8">
              <div>
                Your plan does not include this feature. Buy one of our plans to
                <span>unlock </span> this feature
              </div>
              <a
                onClick={() => {
                  if (lock) {
                    navigate("/settings");
                    dispatch(userSlice.actions.setSettings(2));
                  }
                }}
              >
                Upgrade plan
              </a>
            </div>
          </div>
        </div>

        <motion.h1
          initial={{ x: -100, opacity: 0 }}
          animate={{ x: 0, opacity: 1 }}
          transition={{
            delay: 0.2,
            x: { type: "spring" },
            stiffness: 60,
            opacity: { duration: 1 },
            ease: "easeIn",
            duration: 1,
          }}
        >
          Revolutionize Your <strong>Marketing Campaigns</strong>
          <br /> with our
          <strong> Cutting-Edge AI Workflow Builder!</strong>
        </motion.h1>
        <div className="fx gap-15">
          <motion.button
            initial={{ x: -25, opacity: 0 }}
            animate={{ x: 0, opacity: 1 }}
            viewport={{ once: true }}
            transition={{
              transition: 0.2,
              duration: 0.3,
              x: { type: "spring", stiffness: 60 },
              opacity: { duration: 0.6 },
              ease: "easeIn",
            }}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.85 }}
            onClick={() => {
              if (agentFetchStatus != "fetching") {
                if (lock) {
                  navigate("/settings");
                  dispatch(userSlice.actions.setSettings(2));
                } else navigate("/agent/cynthia");
              }
            }}
          >
            {agentFetchStatus === "fetching" ? (
              <div className="load-container">
                <div className="linespinner ls-white"></div>
              </div>
            ) : lock ? (
              "Upgrade Plan"
            ) : unfinishedChat ? (
              "Continue Chat"
            ) : (
              "Try Now"
            )}
          </motion.button>
          {!lock && unfinishedChat && (
            <motion.button
              className="cancel-btn"
              initial={{ x: -25, opacity: 0 }}
              animate={{ x: 0, opacity: 1 }}
              viewport={{ once: true }}
              transition={{
                transition: 0.2,
                duration: 0.3,
                x: { type: "spring", stiffness: 60 },
                opacity: { duration: 0.6 },
                ease: "easeIn",
              }}
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.85 }}
              onClick={async () => {
                if (lock) {
                  navigate("/settings");
                  dispatch(userSlice.actions.setSettings(2));
                } else {
                  await deleteChat({ agentChatId });
                  navigate("/agent/cynthia");
                }
              }}
            >
              {deleteChatStatus === "pending" ? (
                <div className="load-container">
                  <div className="linespinner"></div>
                </div>
              ) : (
                <span>Start New Chat</span>
              )}
            </motion.button>
          )}
        </div>
      </div>
      <motion.div
        initial={{ x: 100, opacity: 0 }}
        animate={{ x: 0, opacity: 1 }}
        transition={{
          delay: 0.2,
          x: { type: "spring" },
          stiffness: 60,
          opacity: { duration: 1 },
          ease: "easeIn",
          duration: 1,
        }}
        className="nw_banner_right"
      >
        <img
          src="https://dlvkyia8i4zmz.cloudfront.net/GJPGhfRWTamW3YKbi4Wf_Group_1321314635__3_.svg"
          className="web_img"
        />
        <img
          src="https://dlvkyia8i4zmz.cloudfront.net/XlLqj82bSkidplzBDDil_Frame_1618873344__1_.svg"
          className="resp_img"
        />
      </motion.div>
    </div>
  );
};

