import { useEffect, useState } from "react";
import Table from "@/components/layoutElements/table";
import Workspace from "@/components/layoutElements/workspace";
import AuthenticatedLayout from "@/layouts/AuthenticatedLayout";
import NewBanner from "@/components/layoutElements/NewBanner";
import { getCookie, deleteCookies } from "@/services/cookieService";
import introJs from "intro.js";
import WorkflowNew from "@/components/layoutElements/workflowNew";
import { useDispatch, useSelector } from "react-redux";
import { addValue } from "@/redux/User";
import { useLocation } from "react-router-dom";
import CouponcodeModal from "@/components/layoutElements/modals/CouponcodeModal";
import { DashBoardSteps, DashBoardStepsAI } from "@/constants/TourSteps";
import Loader from "@/components/loaders/Loader";
import CreateBot from "@/components/layoutElements/CreateBot";
import environment from "@/constants/Environment";
import { useNavigate } from "react-router-dom";
import { State } from "@/redux/Types";
import HelloBar from "@/components/layoutElements/helloBar";
import { TabTitle } from "@/utils/TitleFuntion";
import WorkspaceMbDropdown from "@/components/layoutElements/WorkspaceMbDropdown";
import useGetPlanDetails from "@/hooks/useGetPlanDetails";
import { motion } from "framer-motion";
import useGetAllWorkSpaces from "@/hooks/useGetWorkSpaces";
import useGetRecentBots from "@/hooks/useGetRecentBots";

const environmentData = environment();

export default function Dashboard() {
  const router = useNavigate();
  const dispatch = useDispatch();
  const [workspaceId, setWorkspaceId] = useState<any>();
  const location = useLocation();
  const userId = useSelector((state: State) => state.user.userId);
  const couponcode = new URLSearchParams(location.search).get("couponcode");
  const [helloBar, setHelloBar] = useState<boolean>(false);
  const [isPopupOpen, setIsPopupOpen] = useState<boolean>(false);
  const [indexFromDropdown, setindexFromDropdown] = useState(null);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const planDetails = useGetPlanDetails();
  const trial = useSelector(
    (state: State) => state.user.plan?.["trial"] ?? null,
  );
  const [componentsLoaded, setComponentsLoaded] = useState<boolean>(false);

  const allWorkSpaces = useGetAllWorkSpaces();
  const recentBots = useGetRecentBots();

  useEffect(() => {
    if(planDetails.isSuccess){
      if (planDetails.data?.plan?.status === "expired" || planDetails.data?.plan?.status === "cancelled") {
        if (planDetails.data?.plan?.plan_code === environmentData.TRIALPLAN) {
          router("/trial_expired");
        } else {
          router("/plan_expired");
        }
      }
    }
    // if (planDetails.isSuccess) {
    //   if (
    //     planDetails.data.plan.status === "expired" ||
    //     planDetails.data.plan.status === "cancelled"
    //   ) {
    //     if (planDetails.data.plan.plan_code === "trial_01") {
    //       router("/trial_expired");
    //     } else {
    //       router("/plan_expired");
    //     }
    //   }
    // }
  }, [planDetails.data]);

  useEffect(() => {
    if (
      !planDetails.isLoading &&
      !allWorkSpaces.isLoading &&
      !recentBots.isLoading
    ) {
      setComponentsLoaded(true);
    }
  }, [planDetails.isLoading, allWorkSpaces.isLoading, recentBots.isLoading]);

  useEffect(() => {
    TabTitle(" | Dashboard");
    if (
      componentsLoaded &&
      getCookie(`journey-dashboard:${userId}`) &&
      window.screen.width >= 1280
    ) {
      deleteCookies(`journey-dashboard:${userId}`, "/");
      const intro = introJs();
      intro.setOptions({
        steps: environmentData.isAI ? DashBoardStepsAI : DashBoardSteps,
        showBullets: false,
        tooltipClass: "customTooltip",
        highlightClass: "customHighlight",
        buttonClass: "customButton",
      });
      intro.onbeforechange(() => {
        if (intro._currentStep === 4) {
          window.scrollTo({ top: 0, behavior: "smooth" });
        }
        if (window.matchMedia("(max-width: 990px)").matches) {
          if (intro._currentStep === 4) {
            dispatch(addValue(""));
          } else {
            dispatch(addValue("app-collapse"));
          }
        }
        return true;
      });
      intro.onafterchange(() => {
        const ele: HTMLElement = document.querySelector(
          ".customTooltip",
        ) as HTMLElement;
        if (!ele) return;
        if (!window.matchMedia("(max-width: 380px)").matches)
          return ele?.classList?.remove("left-zero");
        setTimeout(() => {
          if (intro._currentStep === 4) ele?.classList?.add("left-zero");
          else ele?.classList?.remove("left-zero");
        }, 500);
      });
      intro.start();
    }
  }, [componentsLoaded]);

  // useEffect(() => {
  //   const manualThriveWidgetLoad = (thriveData) => {
  //     var trackingScript = document.createElement("script");
  //     trackingScript.type = "text/javascript";
  //     trackingScript.text = `
  //     var ztUserData = {};
  //     ztUserData['za_email_id'] = '${thriveData?.email}';
  //     ztUserData['user_unique_id'] = '${thriveData?.userId}';
  //     ztUserData['thrive_digest'] = '${thriveData?.digest}';
  //     ztUserData['signUpPage'] = 'https://app.outgrow.chat/register';
  //     ztUserData['signInPage'] = 'https://app.outgrow.chat/login';`;
  //     document.head.appendChild(trackingScript);
  //     if (typeof window.reloadThriveWidget === "function")
  //       window.reloadThriveWidget();
  //   };
  //   manualThriveWidgetLoad(thriveData);
  // }, []);

  const sendWorkspaceId = (workspaceId) => {
    setWorkspaceId(workspaceId);
  };

  const handleIndex = (index) => {
    setindexFromDropdown(index);
  };

  if (planDetails.isLoading || allWorkSpaces.isLoading || recentBots.isLoading)
    return <Loader />;
  if (planDetails.isError) {
    router("/trial_expired");
    return null;
  }

  return (
    <>
      <AuthenticatedLayout>
        <HelloBar helloBar={helloBar} setHelloBar={setHelloBar} />
        <motion.div
          initial={{ opacity: 0, translateY: 20 }}
          animate={{ opacity: 1, translateY: 0 }}
          exit={{ opacity: 0 }}
          transition={{ duration: 0.25 }}
          className={`cards-tables ${trial || trial === null ? "hello-p" : ""}`}
        >
          {!environmentData.isAI && <NewBanner />}
          <WorkflowNew
            workspacedropDownIndex={handleIndex}
            isOpen={isPopupOpen}
            onTogglePopup={() => setIsPopupOpen((prev) => !prev)}
            addClassToBody={() => (document.body.style.overflow = "hidden")}
            onModalOpen={() => setShowCreateModal(true)}
            allWorkSpaces={allWorkSpaces.data}
            recentBots={recentBots.data}
          />
          <Table
            workspaceId={workspaceId}
            workspaces={allWorkSpaces.data}
            // onModalOpen={() => setShowCreateModal(true)}
          />
          <Workspace workspaceId={sendWorkspaceId} />
          <WorkspaceMbDropdown
            isOpen={isPopupOpen}
            onClose={() => setIsPopupOpen(false)}
            receivedIndex={indexFromDropdown}
            removeClassFromBody={() => (document.body.style.overflow = "auto")}
          />
          {couponcode && <CouponcodeModal />}
          {showCreateModal && (
            <CreateBot onClose={() => setShowCreateModal(false)} />
          )}
        </motion.div>
      </AuthenticatedLayout>
    </>
  );
}
