import { Fragment, useEffect } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import Header from "../components/layoutElements/header";
import $ from "jquery";
function AuthenticatedLayout({ children }: any) {
  const router = useNavigate();
  const isLoggedIn = useSelector((state: any) => state.user.isLoggedIn);

  useEffect(() => {
    if (!isLoggedIn) {
      router("/login");
    }
  }, [isLoggedIn]);

  const DropdownClose = (e) => {
    if (e.target.classList[0] === "table-search") {
      return;
    }
    if (
      !e.target.closest(".order_box") &&
      !e.target.closest(".dots") &&
      !e.target.closest(".filter_box") &&
      !e.target.closest(".chat_notify") &&
      !e.target.closest(".dropdown_container")
    ) {
      $(".bot-list-dropdown").addClass("none");
    }
  };

  return (
    <Fragment>
      <div
        className="wrapper fx"
        onClick={(e) => {
          DropdownClose(e);
        }}
      >
        <div className="main-container">
          <Header />
          {children}
        </div>
      </div>
    </Fragment>
  );
}

export default AuthenticatedLayout;
