import { Fragment, useState, useRef, useEffect } from "react";
import $ from "jquery";
import { useDispatch } from "react-redux";
import { axiosPost } from "../../../services/axiosService";
import { userSlice } from "../../../redux/User";
import FolderAnimate from "../../../assets/FolderAnimate";
import environment from "../../../constants/Environment"
import { useQueryClient } from "@tanstack/react-query";

const Addworkspace = () => {
  const dispatch = useDispatch();
  const queryClient = useQueryClient();
  const [workspaceName, setWorkspaceName] = useState<string>("");
  const [reloadState, setReloadState] = useState(false);
  const [workspaceValid, setWorkspaceValid] = useState<any>(false);

  const handleCloseWorkspace = () => {
    $(".workspace-modal").addClass("none");
    $("body").css("overflow", "auto");
    setWorkspaceValid(false);
    setWorkspaceName("");
  };
  const handleCreateWorkspace = (e) => {
    $("body").css("overflow", "hidden");
    if (e.key === "Enter" || e.type === "click") {
      e.preventDefault();
      if (!workspaceValid) return;
      setReloadState(true);
      dispatch(userSlice.actions.setReload(true));
      const name = workspaceName.trimStart().trimEnd();
      axiosPost(`/workspace/addWorkspace`, { workspaceName: name })
        .then((response) => {
          if (response.status == 200) {
            setReloadState(false);
            dispatch(userSlice.actions.setReload(false));
            dispatch(
              userSlice.actions.setToastPopUp({
                show: true,
                toastType: "success",
                message: "Folder created!",
              })
            );
            setWorkspaceName("");
            handleCloseWorkspace();
            queryClient.invalidateQueries({ queryKey: ["workSpaces"] });
          }
        })
        .catch((err) => {
          dispatch(userSlice.actions.setReload(false));
          setReloadState(false);
          dispatch(
            userSlice.actions.setToastPopUp({
              show: true,
              toastType: "error",
              message: err.response.data.message,
            })
          );
          handleCloseWorkspace();
        });
    }
  };

  const validateWorkspaceName = (workspaceName) => {
    var validator = new RegExp(/\s*(?:\S\s*){3,}$/);
    if (validator.test(workspaceName)) {
      setWorkspaceValid(true);
    } else {
      setWorkspaceValid(false);
    }
    setWorkspaceName(workspaceName);
  };

  return (
    <Fragment>
      <div className="workspace-modal none modal-scroll" onClick={handleCloseWorkspace}>
        <div className="workspace-container p-relative" onClick={(e) => e.stopPropagation()}>
          <div className="add-wrk-container">
            <div className={`wrkspace-img-cont-dashboard flex-cnt ${environment().isAI && "bg-white"}`}>
            {environment().isAI ? <img className="new-folder-img" src="https://dlvkyia8i4zmz.cloudfront.net/kiG6nrmjQD6n7m6EWWcy_create_new_folder.svg" alt="" /> : <FolderAnimate />}
              <div className="close new-modal-close flex-cnt" onClick={handleCloseWorkspace}>
                <svg width="10" height="10" viewBox="0 0 10 10" fill="#6D6D6D" xmlns="http://www.w3.org/2000/svg">
                  <path
                    d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                    fill="#6D6D6D"
                  />
                </svg>
              </div>
            </div>

            <div className="workspace-dt no-pb" onKeyDown={(e) => handleCreateWorkspace(e)}>
              <h1>Create New Folder</h1>
              <div className="input-container-wrk">
                <label htmlFor="">Folder Name</label>
                <input
                  onChange={(e) => {
                    validateWorkspaceName(e.target.value);
                  }}
                  onFocus={() => {
                    window.screen.width < 1280 && document.querySelector(".workspace-modal")?.scrollBy(0, 30);
                  }}
                  value={workspaceName}
                  type="text"
                  placeholder="Enter folder name"
                />
                {workspaceName.length > 0 && !workspaceValid ? (
                  <p className="alert-message"> Folder name must be at least three characters </p>
                ) : (
                  <> </>
                )}
              </div>
            </div>
            <div className="create-bot-footer flex-cnt">
              <button
                className={!workspaceValid || workspaceName === undefined ? "valid-workspace" : ""}
                onClick={(e) => handleCreateWorkspace(e)}
                disabled={!workspaceValid}
              >
                {reloadState ? "Creating..." : "Create Folder"}
              </button>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default Addworkspace;
