import { axiosGet } from "@/services/axiosService";
import { useQuery } from "@tanstack/react-query";

export default function useGetRecentBots() {
    return useQuery({
      queryKey: ["bots", "allRecentBot"],
      queryFn: async () => {
        const response = await axiosGet(`/users/allRecentBot`);
        return response.data.data;
      },
      staleTime: 1000,
    });
  }